import returns from '../../common/returns.vue'
export default{
	components:{
		returns
	},
	data(){
		return{
			title:'分销订单',
			cur:0,
			reveal:false,
			data:'',
			data_list:''
		}
	},
	methods:{
		Index(type){
			this.cur = type
			this.service.entire(this,'post',this.APIconfig.api_root.subuser.u_index,{
				user_id:this.$store.state.user.id,
				type:type
			},function(self,res){
				console.log(res)
				self.data = res.data
				self.data_list = res.data.order
			})
		}
	},
	onShow() {
		Index(0)
	}
	
}
